import { PAY_PAYPAL_CALLBACK } from "../../../api/api";
import { useRouter, useRoute } from "vue-router";
import { ref } from "vue";
export default {
  name: "callback",

  setup() {
    const route = useRoute();
    const routeQuery = route.query;
    const successValue = ref(1);
    const failedText = ref('Order paid failed');
    const router = useRouter();

    const goHome = () => {
      router.push('/');
    };

    const goBack = () => {
      router.push('/services/price');
    };

    const goMyProfile = () => {
      router.push({
        path: '/overview',
        query: {
          identity: localStorage.getItem('identity')
        }
      });
    };

    return {
      routeQuery,
      successValue,
      goHome,
      goBack,
      goMyProfile,
      failedText
    };
  },

  data() {
    return {
      showLoadingValue: true
    };
  },

  mounted() {
    this.paypalCallback();
  },

  methods: {
    paypalCallback() {
      let params = {
        success: this.routeQuery.success,
        paymentId: this.routeQuery.paymentId,
        PayerID: this.routeQuery.PayerID
      };
      PAY_PAYPAL_CALLBACK(params).then(res => {
        console.log(res);

        if (res.code === 200) {
          this.successValue = 2;
          this.showLoadingValue = false;
        }
      }).catch(err => {
        console.log(err);

        if (err.code === 400) {
          this.successValue = 3;

          if (err.msg == '取消付款') {
            this.failedText = 'Cancel Payment';
          }
        }

        this.showLoadingValue = false;
      });
    }

  }
};